"use client"

import { useState, useEffect } from "react"
import { useRouter } from "next/navigation"
import Link from "next/link"
import Image from "next/image"
import { Card, CardHeader, CardTitle, CardContent } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Badge } from "@/components/ui/badge"
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { Textarea } from "@/components/ui/textarea"
import { 
  Calendar, Clock, DollarSign, Plus, CheckCircle, 
  User, MessageSquare, LayoutDashboard, Settings, History, Goal
} from "lucide-react"
import { 
  Dialog, DialogContent, DialogHeader, DialogTitle, DialogFooter 
} from "@/components/ui/dialog"

// --- 인터페이스 ---
interface ConsultationProduct {
  id: string;
  title: string;
  duration: string;
  price: number;
  description: string;
}

interface Reservation {
  id: string;
  userName: string;
  date: string;
  time: string;
  status: "PAID" | "COMPLETED" | "CANCELLED";
}

export default function AdvisorDashboard() {
  const router = useRouter()
  const [isMounted, setIsMounted] = useState(false)
  
  // 상태 관리
  const [products, setProducts] = useState<ConsultationProduct[]>([
    { id: "1", title: "1:1 진로 고민 집중 상담", duration: "50분", price: 50000, description: "취업 및 진로 방향성에 대한 심도 있는 상담을 제공합니다." }
  ])
  const [reservations, setReservations] = useState<Reservation[]>([
    { id: "R1", userName: "이철수", date: "2025-12-20", time: "14:00", status: "PAID" },
    { id: "R2", userName: "박영희", date: "2025-12-21", time: "10:00", status: "PAID" }
  ])

  const [isAddProductOpen, setIsAddProductOpen] = useState(false)
  const [newProduct, setNewProduct] = useState({ title: "", duration: "", price: "", description: "" })

  useEffect(() => {
    // 전문가 권한 체크 (간단 예시)
    const role = localStorage.getItem("role")
    if (role !== "ADVISOR" && role !== "MANAGER") { // 전문가 전용
       // router.push("/login") 
    }
    setIsMounted(true)
  }, [])

  const handleAddProduct = () => {
    const product: ConsultationProduct = {
      id: Math.random().toString(),
      ...newProduct,
      price: Number(newProduct.price)
    }
    setProducts([...products, product])
    setIsAddProductOpen(false)
    setNewProduct({ title: "", duration: "", price: "", description: "" })
  }

  if (!isMounted) return null

  return (
    <div className="min-h-screen bg-[#F8F9FA]">
      {/* 상단 네비게이션 */}
      <header className="border-b bg-white sticky top-0 z-50">
        <div className="container mx-auto px-4 h-16 flex items-center justify-between">
          <div className="flex items-center gap-8">
            <Link href="/">
              <Image src="/logo.png" alt="Logo" width={100} height={30} />
            </Link>
            <nav className="hidden md:flex gap-6 text-sm font-medium">
              <Link href="/goals" className="text-muted-foreground hover:text-primary">목표 관리</Link>
              <Link href="/history" className="text-muted-foreground hover:text-primary">거래 내역</Link>
              <Link href="/advisor/dashboard" className="text-primary font-bold">전문가 센터</Link>
            </nav>
          </div>
          <div className="flex items-center gap-4">
            <Badge variant="secondary" className="bg-primary/10 text-primary">전문가 모드</Badge>
            <div className="w-8 h-8 rounded-full bg-slate-200 border" />
          </div>
        </div>
      </header>

      <main className="container mx-auto px-4 py-8">
        <div className="flex flex-col md:flex-row justify-between items-start md:items-center mb-8 gap-4">
          <div>
            <h1 className="text-2xl font-bold tracking-tight">전문가 관리 센터</h1>
            <p className="text-muted-foreground">상담 상품을 관리하고 예약 내역을 확인하세요.</p>
          </div>
          <Button onClick={() => setIsAddProductOpen(true)}>
            <Plus className="w-4 h-4 mr-2" /> 상담 상품 등록
          </Button>
        </div>

        <div className="grid lg:grid-cols-4 gap-8">
          {/* 사이드바 요약 */}
          <div className="lg:col-span-1 space-y-4">
            <Card>
              <CardContent className="p-6">
                <div className="space-y-4">
                  <div className="flex justify-between items-center text-sm">
                    <span className="text-muted-foreground">누적 상담</span>
                    <span className="font-bold">24건</span>
                  </div>
                  <div className="flex justify-between items-center text-sm">
                    <span className="text-muted-foreground">이번 달 수익</span>
                    <span className="font-bold text-primary">₩1,240,000</span>
                  </div>
                  <div className="flex justify-between items-center text-sm">
                    <span className="text-muted-foreground">평균 평점</span>
                    <span className="font-bold">⭐ 4.9</span>
                  </div>
                </div>
              </CardContent>
            </Card>
            
            <div className="grid grid-cols-1 gap-2">
              <Button variant="outline" className="justify-start" asChild>
                <Link href="/settings"><Settings className="w-4 h-4 mr-2" /> 계정 연동 설정</Link>
              </Button>
              <Button variant="outline" className="justify-start" asChild>
                <Link href="/goals"><Goal className="w-4 h-4 mr-2" /> 나의 목표</Link>
              </Button>
            </div>
          </div>

          {/* 메인 탭 영역 */}
          <div className="lg:col-span-3">
            <Tabs defaultValue="reservations">
              <TabsList className="grid w-full grid-cols-2 mb-6">
                <TabsTrigger value="reservations" className="font-bold">예약 내역 (결제 완료)</TabsTrigger>
                <TabsTrigger value="products" className="font-bold">상담 상품 관리</TabsTrigger>
              </TabsList>

              {/* 예약 내역 탭 */}
              <TabsContent value="reservations" className="space-y-4">
                {reservations.map((res) => (
                  <Card key={res.id} className="hover:border-primary/50 transition-colors">
                    <CardContent className="p-6 flex flex-col md:flex-row justify-between items-center gap-4">
                      <div className="flex items-center gap-4">
                        <div className="bg-primary/10 p-3 rounded-full">
                          <User className="w-6 h-6 text-primary" />
                        </div>
                        <div>
                          <div className="flex items-center gap-2">
                            <p className="font-bold text-lg">{res.userName} 님</p>
                            <Badge className="bg-green-500">결제완료</Badge>
                          </div>
                          <div className="flex items-center gap-3 text-sm text-muted-foreground mt-1">
                            <span className="flex items-center gap-1"><Calendar className="w-4 h-4" /> {res.date}</span>
                            <span className="flex items-center gap-1"><Clock className="w-4 h-4" /> {res.time}</span>
                          </div>
                        </div>
                      </div>
                      <div className="flex gap-2 w-full md:w-auto">
                        <Button variant="outline" className="flex-1 md:flex-none">상담 채팅</Button>
                        <Button className="flex-1 md:flex-none">상담 완료 처리</Button>
                      </div>
                    </CardContent>
                  </Card>
                ))}
              </TabsContent>

              {/* 상품 관리 탭 */}
              <TabsContent value="products" className="grid md:grid-cols-2 gap-4">
                {products.map((product) => (
                  <Card key={product.id}>
                    <CardHeader>
                      <CardTitle className="text-lg flex justify-between">
                        {product.title}
                        <Badge variant="outline">{product.duration}</Badge>
                      </CardTitle>
                    </CardHeader>
                    <CardContent>
                      <p className="text-sm text-muted-foreground mb-4 line-clamp-2">{product.description}</p>
                      <div className="flex justify-between items-center">
                        <span className="text-xl font-bold">₩{product.price.toLocaleString()}</span>
                        <Button variant="ghost" size="sm" className="text-primary font-bold">수정하기</Button>
                      </div>
                    </CardContent>
                  </Card>
                ))}
              </TabsContent>
            </Tabs>
          </div>
        </div>
      </main>

      {/* 상품 등록 모달 */}
      <Dialog open={isAddProductOpen} onOpenChange={setIsAddProductOpen}>
        <DialogContent className="sm:max-w-[500px]">
          <DialogHeader>
            <DialogTitle className="text-2xl font-bold">새 상담 상품 등록</DialogTitle>
          </DialogHeader>
          <div className="grid gap-6 py-4">
            <div className="grid gap-2">
              <Label htmlFor="title">상담 제목</Label>
              <Input id="title" placeholder="예: 30분 집중 코칭" value={newProduct.title} onChange={(e)=>setNewProduct({...newProduct, title: e.target.value})} />
            </div>
            <div className="grid grid-cols-2 gap-4">
              <div className="grid gap-2">
                <Label htmlFor="duration">상담 시간</Label>
                <Input id="duration" placeholder="예: 50분" value={newProduct.duration} onChange={(e)=>setNewProduct({...newProduct, duration: e.target.value})} />
              </div>
              <div className="grid gap-2">
                <Label htmlFor="price">상담 가격 (₩)</Label>
                <Input id="price" type="number" placeholder="50000" value={newProduct.price} onChange={(e)=>setNewProduct({...newProduct, price: e.target.value})} />
              </div>
            </div>
            <div className="grid gap-2">
              <Label htmlFor="desc">상품 상세 설명</Label>
              <Textarea id="desc" placeholder="상담 방식이나 다루는 주제를 적어주세요." className="min-h-[100px]" value={newProduct.description} onChange={(e)=>setNewProduct({...newProduct, description: e.target.value})} />
            </div>
          </div>
          <DialogFooter>
            <Button variant="outline" onClick={() => setIsAddProductOpen(false)}>취소</Button>
            <Button onClick={handleAddProduct}>상품 등록하기</Button>
          </DialogFooter>
        </DialogContent>
      </Dialog>
    </div>
  )
}